.app__footer{
    width: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    background-color: var(--color-blue);
    font-family: var(--font-base);
}

.app__footer-links{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 3rem;
    /* padding: 0 2rem; */
    border-bottom: 2.66px solid var(--color-golden);
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work{
    flex: 1;
    margin: 1rem;
    text-align: center;
}

.app__footer-links_logo img:first-child{
    width: 75%;
    margin-bottom: 0.75rem;
}

.app__footer-links_work p:nth-child(2n+1){
    margin-bottom: 1rem;
}

.app__footer-links_icons{
    margin-top: 0.5rem;
}

.app__footer-links_icons svg{
    color: var(--color-golden);
    margin: 0.9rem 0.9rem 3rem 0.9rem;
    font-size: 25px;
    cursor: pointer;
}

.app__footer-links_icons svg:hover{
    color: var(--color-white);
}

.app__footer-headtext{
    font-family: var(--font-base);
    color: var(--color-white);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-size: 32px;
    line-height: 41.6px;
    margin-bottom: 1rem;
}


.app__footer-links_work1 .custom__buttonYellow{
    margin-top: 2rem;
}

.app__footer .p__opensans{
    max-width: none;
    color: var(--color-white);
}
.footer__copyright{
    margin: 1rem 0;
}

@media screen and (min-width: 2000px){
    .app__footer-headtext{
        font-size: 51px;
        line-height: 61px;
    }
}

@media screen and (max-width: 1150px){
    .app__footer-links{
        align-items: center;
        flex-direction: column;
        padding: 0;
    }
    .app__footer-links_contact,
    .app__footer-links_logo,
    .app__footer-links_work{
        margin: 0 2rem;
        width: 100%;
}
}
@media screen and (max-width: 650px){


}

@media screen and (max-width: 350px){
    .app__footer-links_logo img:first-child{
        width: 80%;
    }
}

